import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
//import { of } from 'rxjs/observable/of';
import { Subject } from 'rxjs/Subject';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class NameService {

  static namevalue = 'default';
  private subject = new Subject<string>();
  private car = new Subject<any>();
  private journeyPage =  new BehaviorSubject<string>('');
  constructor() { }

  getName(): Observable<string> {
    return this.subject.asObservable();
  }

  setName(name) {
     this.subject.next(name);
  }

  
  getPageName(): Observable<string> {
    return this.journeyPage.asObservable();
  }

  setPageName(name) {
     this.journeyPage.next(name);
  }
  setCar(data: any[]) {
    this.car.next(data);
  //  console.log(data);
  }

  getCar() {
    return this.car.asObservable();
  }

}
